import { Component, OnInit,Input } from '@angular/core';
import { DownloadPdfProvider } from 'src/providers/download-pdf/download-pdf';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { UtilService } from 'src/providers/utils/utils.service';
import { ConfigProvider } from 'src/providers/utils/config';

@Component({
  selector: 'app-statements',
  templateUrl: './statements.page.html',
  styleUrls: ['./statements.page.scss'],
})
export class StatementsPage implements OnInit {

   @Input() statementsData: any;

  constructor(public downloadFile : DownloadPdfProvider,public logger:LoggerServiceProvider, public utils :UtilService, private config: ConfigProvider) {
    this.utils.analytics("Statements"); 
    this.config.currentPage.pageName = "StatementsPage";
  }
  ngOnInit() {
  
  }
  downloadAndOpenPdf = function(statement){
    this.downloadFile.downloadStatementFile(statement.link, statement.policyNumber);      
}
}
